@font-face {
  font-family: "futura";
  src: url("../fonts/futura-bt-for-dyson-w05-medium.woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

*:active,
*:focus {
  outline: none;
}

html {
  min-height: 100vh;
  overflow-x: hidden;
}

body {
  font-family: "futura";
  font-style: normal;
  font-weight: 400;
  color: #000000;
  background-color: #f9f9f9;
  overflow-x: hidden;
}
body.modal_open {
  height: 100vh;
  overflow: hidden;
}

h1, h2, h3, h4 {
  margin: 0;
}

a, input {
  font-family: "futura", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  text-decoration: none;
  color: inherit;
  border: none;
}

a:hover {
  color: #bd016f;
}

input:focus {
  outline: none;
}

button {
  padding: 0;
  background: none;
  border: none;
}

p {
  margin: 0;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

section {
  position: relative;
}

@media (max-width: 1536px) {
  .from-xl {
    display: none !important;
  }
}
@media (max-width: 1024px) {
  .from-lg {
    display: none !important;
  }
}
@media (max-width: 992px) {
  .from-md {
    display: none !important;
  }
}
@media (max-width: 768px) {
  .from-sm {
    display: none !important;
  }
}
@media (max-width: 420px) {
  .from-xs {
    display: none !important;
  }
}

@media (min-width: 1536px) {
  .to-xl {
    display: none !important;
  }
}
@media (min-width: 1024px) {
  .to-lg {
    display: none !important;
  }
}
@media (min-width: 992px) {
  .to-md {
    display: none !important;
  }
}
@media (min-width: 768px) {
  .to-sm {
    display: none !important;
  }
}
@media (min-width: 420px) {
  .to-xs {
    display: none !important;
  }
}

