.btn {
  font-size: 0.9375vw;
  line-height: 1.0416666667vw;
  color: #FFFFFF;
  width: max-content;
  padding: 0.7291666667vw 1.875vw 0.8333333333vw;
  background-color: #bd016f;
  border: none;
  border-radius: 2.6041666667vw;
  display: block;
  position: relative;
  z-index: 1;
  overflow: hidden;
}
@media (max-width: 1536px) {
  .btn {
    font-size: 1.171875vw;
    line-height: 1.3020833333vw;
    padding: 0.9114583333vw 2.34375vw 1.0416666667vw;
    border-radius: 3.2552083333vw;
  }
}
@media (max-width: 1024px) {
  .btn {
    font-size: 1.7578125vw;
    line-height: 1.953125vw;
    padding: 1.3671875vw 3.515625vw 1.5625vw;
    border-radius: 4.8828125vw;
  }
}
@media (max-width: 768px) {
  .btn {
    font-size: 2.34375vw;
    line-height: 2.6041666667vw;
    padding: 1.8229166667vw 4.6875vw 2.0833333333vw;
    border-radius: 6.5104166667vw;
  }
}
@media (max-width: 420px) {
  .btn {
    font-size: 5vw;
    line-height: 5.5555555556vw;
    padding: 3.8888888889vw 10vw 4.4444444444vw;
    border-radius: 13.8888888889vw;
  }
}
.btn::before {
  content: "";
  width: 0;
  height: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #730243;
  transition: 0.3s ease;
  z-index: -1;
  border-radius: 50%;
}
.btn:hover {
  color: #FFFFFF;
}
.btn:hover::before {
  width: 150%;
  height: 500%; /* 150% and 500% because div completely to be fill the space of the button.*/
  transition: 0.5s ease;
}

